import { isDisabled } from "@testing-library/user-event/dist/utils";
import React, { useState, useRef, useEffect } from "react";
import {
  PageContainer,
  WorkerList,
  WorkerItem,
  WorkerForm,
  Buttons,
  TabButton,
  PlanButton,
  KillTheWorker,
} from "./homeStyles";
import { workers } from "./workersData";

var digSpeed = 0;
for (let i = 0; i < workers.length; i++) {
  if (workers[i].gender != null) {
    if (workers[i].gender == "M") {
      digSpeed += 1;
      // console.warn(parseInt(workers.length) + " M " + parseInt(i));
    } else if (workers[i].gender == "F") {
      digSpeed += 0.5;
      // console.warn(parseInt(workers.length) + " F " + parseInt(i));
    }
    // console.warn("dig speed " + parseFloat(workersDigSpeed));
  }
}

export default function Home() {
  const renderCount = useRef(0);
  const workersCount = useRef(workers.length);
  const [valid, setValid] = useState(false);
  const [listOfWorkers, setListOfWorkers] = useState(workers);
  const [activeTab, setActiveTab] = useState("list-of-workers");
  const [workOrder, setWorkOrder] = useState({
    meters: 2,
    hours: 1,
  });
  const [tempWorkOrder, setTempWorkOrder] = useState({
    meters: "",
    hours: "",
  });

  const [addWorker, setAddWorker] = useState({
    id: workersCount.current + 1,
    name: "",
    surname: "",
    gender: "",
  });

  function computeDigSpeed(gen) {
    if (gen == "M") {
      digSpeed += 1;
      console.warn("updated dig speed " + parseFloat(digSpeed));
    } else if (gen == "F") {
      digSpeed += 0.5;
      console.warn("updated dig speed " + parseFloat(digSpeed));
    }
  }
  const handleChange = (e) => {
    setAddWorker({ ...addWorker, [e.target.name]: e.target.value });
    // verifyData(addWorker);
  };
  const handleAdd = (e) => {
    e.preventDefault();
    if (verifyData(addWorker)) {
      setListOfWorkers((listOfWorkers) => {
        return [...listOfWorkers, addWorker];
      });
      workersCount.current++;
      setAddWorker({
        id: workersCount.current + 1,
        name: "",
        surname: "",
        gender: "",
      });
      computeDigSpeed(addWorker.gender);
    } else {
      console.warn("Incorrect input format.");
    }
  };

  const handleDelete = (id) => {
    setListOfWorkers(listOfWorkers.filter((worker) => worker.id != id));
  };

  const handleOrder = (e) => {
    setTempWorkOrder({ ...tempWorkOrder, [e.target.name]: e.target.value });
  };

  const updateOrder = async () => {
    const orderValue = tempWorkOrder;
    let newOrderValue = {};
    // orderValue = {food: "", vaccine: "", pills: ""}
    const keys = Object.keys(orderValue);
    // keys = ['food', 'vaccine', 'pills']
    // key = keys[1]
    keys.map((key) => {
      // orderValue.vaccine
      if (parseInt(orderValue[key])) {
        newOrderValue[key] =
          parseInt(workOrder[key]) + parseInt(orderValue[key]);
      } else {
        newOrderValue[key] = parseInt(workOrder[key]);
      }
    });
    await setWorkOrder(newOrderValue);
    await setTempWorkOrder({ meters: "", hours: "" });
    console.log(workOrder);
  };

  const verifyData = (data) => {
    if (data.gender != "M" && data.gender != "F") {
      return setValid(false);
    }
    if (data.name === "" || data.name.trim().length <= 0) {
      return setValid(false);
    }
    if (data.surname === "" || data.surname.trim().length <= 0) {
      return setValid(false);
    }
    //setValid(true);
    return true;
  };

  const switchTab = (e, newValue) => {
    e.preventDefault();
    const newActiveTab = newValue;
    setActiveTab(newActiveTab);
  };

  console.log(digSpeed);

  return (
    <PageContainer>
      
      <Buttons>
        <TabButton
          name="list-of-workers"
          activeTab={activeTab}
          onClick={(event) => {
            switchTab(event, "list-of-workers");
          }}
        >
          Seznam zaměstnanců
        </TabButton>
        <TabButton
          name="work-load"
          activeTab={activeTab}
          onClick={(event) => {
            switchTab(event, "work-load");
          }}
        >
          Zakázky
        </TabButton>
      </Buttons>
      {activeTab === "list-of-workers" && (
        <>
          <WorkerList name="workerList">
            {listOfWorkers.map((worker) => (
              <WorkerItem key={worker.id} name={worker.name}>
                {worker.name} {worker.surname} ({worker.gender})
                <KillTheWorker
                  onClick={() => {
                    handleDelete(worker.id);
                  }}
                >
                  X
                </KillTheWorker>
              </WorkerItem>
            ))}
          </WorkerList>
          <WorkerForm name="workerForm">
            <input
              type="text"
              placeholder="jméno"
              className="inputClass"
              name="name"
              value={addWorker.name}
              onChange={handleChange}
            />
            <input
              type="text"
              placeholder="přijímení"
              className="inputClass"
              name="surname"
              value={addWorker.surname}
              onChange={handleChange}
            />
            <input
              type="text"
              placeholder="pohlaví"
              className="inputClass"
              name="gender"
              value={addWorker.gender}
              onChange={handleChange}
            />
            <button className="inputClass" onClick={handleAdd}>
              Přidat
            </button>
          </WorkerForm>
        </>
      )}
      {activeTab === "work-load" && (
        <>
          <WorkerForm style={{ flexDirection: "column " }}>
            <div
              className="inputClass"
              style={{ color: "black", height: "auto" }}
            >
              <h2>Současné zakázky</h2>
              <p>Rychlost práce: {digSpeed} m/h</p>
              <hr></hr>
              <br />
              <p>Celkový počet metrů: {workOrder.meters} metrů</p>
              <p>Časový fond: {workOrder.hours} hodin</p>
            </div>
            <input
              type="number"
              placeholder="metry k vykopání"
              className="inputClass"
              name="meters"
              value={tempWorkOrder.meters}
              onChange={handleOrder}
            />
            <input
              type="number"
              placeholder="čas na výkop (h)"
              className="inputClass"
              name="hours"
              value={tempWorkOrder.hours}
              onChange={handleOrder}
            />
            <button className="inputClass" onClick={updateOrder}>
              Upravit současnou zakázku
            </button>
            <br></br>
            <PlanButton
              workable={parseFloat(
                workOrder.meters / (digSpeed * workOrder.hours)
              )}
              className="inputClass"
              onClick={updateOrder}
              disabled={isDisabled}
            >
              Zadat novou zakázku
            </PlanButton>
          </WorkerForm>
        </>
      )}
    </PageContainer>
  );
}
