import styled from "styled-components";

export const PageContainer = styled.div`
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 100%;
  align-items: center;
  background-color: gray;
`;

export const WorkerList = styled.div`
  display: flex;
  flex-direction: column;
  width: 650px;
  background: trasparent;
`;

export const WorkerItem = styled.div`
  display: block;
  height: 45px;
  padding: 0 15px;
  align-items: center;
  justify-content: center;
  padding-top: 10px;
  justify-content: space-between;
  background-color: red;
  background-color: #f4f2f3;
  &:nth-child(even) {
    background-color: rgba(80, 146, 133, 0.9);
  }
`;

export const WorkerForm = styled(WorkerList)`
  flex-direction: row;
  justify-content: left;
  text-align: start;
  margin: 40px 0;
  padding-top: 0;
  justify-content: space-between;
  align-items: center;
`;

export const Buttons = styled(WorkerForm)`
  margin: 30px 0;
  height: 40px;
  display: flex;
`;

export const TabButton = styled.button`
  border-radius: 10px;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 40%;
  border: 1px solid yellow;
  color: white;
  font-size: 20px;
  cursor: pointer;
  background-color: transparent;
  ${(props) => {
    if (props.name === props.activeTab) {
      return `
        background-color: rgba(255, 255, 255, 0.3);
      `;
    }
  }}
`;

export const PlanButton = styled.button`
  display: flex;
  border-radius: 10px;
  text-align: center;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 48%;
  border: 1px solid white;
  color: white;
  font-size: 20px;
  padding: 5px 10px;
  cursor: pointer;
  background-color: transparent;
  ${(props) => {
    if (props.workable <= 1) {
      return `
            background-color: rgba(0, 255, 0, 0.3);
        `;
    } else {
      return `
            background-color: rgba(255, 0, 0, 1);
            pointer-event:none;
        `;
    }
  }}
`;

export const KillTheWorker = styled.div`
  display: flex;
  position: relative;
  z-index: 50;
  height: 20px;
  width: 20px;
  margin: 0;
  padding: 0;
  align-items: center;
  justify-content: center;
  color: rgb(200, 10, 30);
  top: -20px;
  border-radius: 50%;
  border: 1px solid black;
  cursor: pointer;
`;